const USERSTATE_NAME = "NAME";
const USERSTATE_MODE = "MODE";

const BOARD_CREATE = "CREATE";
const BOARD_DELETE = "DELETE";
const BOARD_READ = "READ";

const userState = {
  name: "ÏùµÎ™Ö",
  mode: "day",
};

const boardState = {
  total: 3,
  boards: [
    {
      brdnum: 1,
      writer: "Îâ¥ÎπÑ",
      title: "Í∞ÄÏûÖÏù∏ÏÇ¨Ìï©ÎãàÎã§~",
      desc: "ÏïàÎÖïÌïòÏÑ∏Ïöî~ Ïûò Î∂ÄÌÉÅÎìúÎ†§Ïöî üòÅ",
      date: new Date(),
    },
    {
      brdnum: 2,
      writer: "Î¶¨ÏûòÏïå",
      title: "Î¶¨Ïï°Ìä∏?",
      desc: "Î¶¨Ïï°Ìä∏Îäî Ïû¨Î∞åÏñ¥",
      date: new Date(),
    },
    {
      brdnum: 3,
      writer: "Ïñ¥Í∑∏Î°ú",
      title: "Îì§Ïñ¥Ïò§ÏÑ∏Ïöî~~",
      desc: "ÎÇòÍ∞ÄÏÑ∏Ïöî üòä",
      date: new Date(),
    },
  ],
  selectedBoard: {},
};

// reducers
export function userState_ruducer(state = userState, action) {
  switch (action.type) {
    case USERSTATE_NAME: {
      return {
        ...state,
        name: action.name,
      };
    }
    case USERSTATE_MODE: {
      return {
        ...state,
        mode: action.mode,
      };
    }
    default:
      return state;
  }
}

// CRUD Ï≤òÎ¶¨Î•º reducer Î©îÏÜåÎìúÏóêÏÑú Î™®Îëê Ï≤òÎ¶¨
export function board_reducer(state = boardState, action) {
  let boards = state.boards;

  console.log("state\n", state);
  console.log("action\n", action);

  switch (action.type) {
    case BOARD_CREATE: {
      let data = action.data;
      let total = state.total;

      // ÏÉàÎ°úÏö¥ ÏÇΩÏûÖ
      if (data.brdnum === -1) {
        return {
          total: total + 1,
          boards: boards.concat({ ...data, brdnum: ++total, date: new Date() }),
          selectedBoard: {},
        };
      }

      // Update
      return {
        ...state,
        boards: boards.map((row) =>
          data.brdnum === row.brdnum ? { ...data } : row
        ),
        selectedBoard: {},
      };
    }
    case BOARD_DELETE: {
      let total = state.total;
      let initNum = 1;
      // ÏÇ≠Ï†úÌï† row ÎπºÍ≥† Îã§Ïãú Î∞∞Ïó¥Î°ú ÎßåÎì§Í∏∞
      return {
        total: total - 1,
        boards: boards
          .filter((row) => row.brdnum !== action.brdnum)
          .map((row) => {
            row.brdnum = initNum++;
            if (initNum === total) initNum = 1;
            return row;
          }),
        selectedBoard: {},
      };
    }
    case BOARD_READ:
      return {
        ...state,
        selectedBoard: boards.find((row) => row.brdnum === action.brdnum),
      };
    default:
      return state;
  }
}
